<template>
  <div class="login">
    <v-card :loading="loading" class="mx-auto my-12" max-width="374">
      <template slot="progress">
        <v-progress-linear
          color="deep-purple"
          height="10"
          indeterminate
        ></v-progress-linear>
      </template>
      <v-card-title>Backstage - Login </v-card-title>
      <v-card-text>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="form.username"
            :counter="20"
            :rules="usernameRules"
            label="Username"
            :disabled="loading"
            required
          ></v-text-field>

          <v-text-field
            v-model="form.password"
            :counter="20"
            :rules="passwordRules"
            label="Password"
            required
            type="password"
            :disabled="loading"
            @keydown.enter="validate()"
          ></v-text-field>

          <v-btn
            :disabled="!valid"
            color="success"
            class="mr-4"
            @click="validate"
          >
            
          </v-btn>

          <v-btn color="error" class="mr-4" @click="reset">  </v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </div>
</template>

<style>
</style>

<script>
import Login from "./login";
export default Login;
</script>
